<template>
	<div>
		<HeaderComponent :view-title="awardOffTitle"></HeaderComponent>
		<div id="awardOff-main">
			<section class="awardOff-section-top">
				<h4>选择活动</h4>
				<div class="selectBox">
					<div class="chooseParent">
						<select>
							<option>请选择活动</option>
							<option>活动1</option>
							<option>活动2</option>
						</select>
						<span></span>
					</div>
					<div class="redeemCode">
						<input type="text" name="RedeemCode" placeholder="请输入兑换码">
					</div>
					<div class="submitBtn" @click="showDialog()">立即提交</div>
				</div>
			</section>
			<section class="awardOff-section-bottom">
				<h4>核销记录</h4>
				<ul class="record">
					<li class="clear">
						<p>核销时间</p>
						<p>兑换码</p>
						<p>中奖详情</p>
					</li>
					<li class="clear" v-for="(items,index) in awardOffData">
						<p>
							<span class="time">{{items.time}}</span><span class="Rcode">{{items.rcode}}</span><span class="details" @click="showDetails(items,index)">查看</span>
						</p>
						<!-- <p>xic5an</p>
						<p>查看</p> -->
						<ul class="details-list" v-show="items.show">
							<li v-for="item in items.details"><span>{{item.name}}：</span><span>{{item.msg}}</span></li>
							<!-- <li><span>奖品名称：</span><span>5元抵扣券</span></li>
							<li><span>中奖者编号：</span><span>sfj0owfgdajf204g</span></li>
							<li><span>中奖时间：</span><span>2018-08-22 10:00</span></li> -->
						</ul>
					</li>
				</ul>
			</section>
		</div>
		<!-- 弹出模态框 -->
		<div class="dialog-modal" v-show="isShowDialog" @touchmove.prevent>
			<div class="dialog-wrapper">
				<!-- <transition name="dialog"> -->
					<ul class="dialog-container">
						<li class="container-title">提示</li>
						<li class="tips">该兑换码对应奖品为：5元抵扣卷，确认核销？</li>
					</ul>
					<div class="dialog-close-btn clear">
						<p class="left closeD"><span @click="closeDialog()">取消</span></p>
						<p class="left removeD"><span @click="doCancel()">是的</span></p>
					</div>
				<!-- </transition> -->
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		awardOffTitle:Object,
		awardOffData:Object,
		data(){
			return{
				awardOffTitle:{
					name:'奖品核销',
					showCode:1
				},
				isShowDialog:false,
				awardOffData:[
					{time:'2018-08-22 10:00',rcode:'xic5an',details:[
						{name:'活动名称',msg:'超市大促销'},
						{name:'奖品名称',msg:'5元抵扣券'},
						{name:'中奖者编号',msg:'sfj0owfgdajf204g'},
						{name:'中奖时间',msg:'2018-08-22 10:00'},
						],
						show:false
					},
					{time:'2018-08-22 10:00',rcode:'xic5an',details:[
						{name:'活动名称',msg:'超市大促销'},
						{name:'奖品名称',msg:'5元抵扣券'},
						{name:'中奖者编号',msg:'sfj0owfgdajf204g'},
						{name:'中奖时间',msg:'2018-08-22 10:00'},
						],
						show:false
					},
					{time:'2018-08-22 10:00',rcode:'xic5an',details:[
						{name:'活动名称',msg:'超市大促销'},
						{name:'奖品名称',msg:'5元抵扣券'},
						{name:'中奖者编号',msg:'sfj0owfgdajf204g'},
						{name:'中奖时间',msg:'2018-08-22 10:00'},
						],
						show:false
					},
					{time:'2018-08-22 10:00',rcode:'xic5an',details:[
						{name:'活动名称',msg:'超市大促销'},
						{name:'奖品名称',msg:'5元抵扣券'},
						{name:'中奖者编号',msg:'sfj0owfgdajf204g'},
						{name:'中奖时间',msg:'2018-08-22 10:00'},
						],
						show:false
					}
				]
			}
		},
		mounted(){},
		methods:{
			showDetails:function(item,index){
				this.awardOffData.forEach( data => {
					if(data.show !== this.awardOffData[index].show){
						data.show = false;
					}
				});
				item.show = !item.show;
			},
			showDialog:function(){
				this.isShowDialog = !this.isShowDialog;
			},
			closeDialog:function(){
				this.isShowDialog = false;
			},
			doCancel:function(){
				this.isShowDialog = false;
				alert("已核销");
			}
		},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	.iconawardOff {
	   width: 0.8rem; height: 0.8rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#F35353;
	   @include center;
	}
	#awardOff-main{
		margin-top:2.2rem;
	}
	.awardOff-section-top{
		h4{
			@include sc(0.7rem,#4C4C4C);
			padding-left:0.5rem;
			height:1.8rem;
			line-height:1.8rem;
		}
		.selectBox{
			background:#fff;
			position:relative;
			&::before,&::after{
				content: "";
				display:table;
			}
			.chooseParent,.redeemCode{
				width:9rem;
				overflow: hidden;
				position:relative;
				border:1px solid #CACACA;
				margin-left:1rem;
				span{
					height:1.6rem;
					position:absolute;
					top:0;
					right:1.5rem;
					border-left:1px solid #CACACA;
				}
			}
			.chooseParent{
				margin-bottom:0.6rem;
				margin-top:1rem;
			}
			.redeemCode{
				margin-bottom:1rem;
				input{
					width:9rem;
					height:1.6rem;
					line-height: 1.6rem;
					font-size:0.7rem;
					padding-left:0.3rem;
					&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
					  @include sc(0.7rem,#A1A1A1);
					}
					&::-moz-placeholder { /* Firefox 19+ */
					  @include sc(0.7rem,#A1A1A1);
					}
					&:-ms-input-placeholder { /* IE 10+ */
					  @include sc(0.7rem,#A1A1A1);
					}
					&:-moz-placeholder { /* Firefox 18- */
					  @include sc(0.7rem,#A1A1A1);
					}
				}
			}
			select{
				width:10rem;
				height:1.6rem;
				padding-left:0.3rem;
				@include sc(0.7rem,#4C4C4C);
				background:url('../../assets/images/arrow.png') right center no-repeat;
				background-size:1rem;
				background-position-x:7.8rem;
				option{
					@include sc(0.7rem,#4C4C4C);
				}
			}
			.submitBtn{
				width:3.5rem;
				height:1.6rem;
				line-height: 1.6rem;
				@include sc(0.7rem,#fff);
				@include borderRadius(0.1rem);
				background:#F35353;
				text-align:center;
				position:absolute;
				bottom:1rem;
				right:0.5rem;
			}
		}
	}
	.awardOff-section-bottom{
		h4{
			@include sc(0.7rem,#4C4C4C);
			padding-left:0.5rem;
			height:1.8rem;
			line-height:1.8rem;
		}
		ul.record{
			width:100%;
			padding:0 0.5rem;
			background:#fff;
			li{
				width:100%;
				margin:0 auto;
				/* p{
					width:40%;
					height:1.8rem;
					line-height: 1.8rem;
					text-align:center;
					float:left;
					@include sc(0.7rem,#929292);
				}
				p:last-child{
					width:20%;
				} */
			}
			li:first-child{
				P{	
					width:40%;
					height:1.5rem;
					line-height: 1.5rem;
					text-align:center;
					float:left;
					@include sc(0.7rem,#4C4C4C);
					border-bottom:1px solid #D9D9D9;
				}
				p:last-child{
					width:20%;
				}
			}
			li:not(:first-child) p{
				width:100%;
				height:1.8rem;
				line-height: 1.8rem;
				border-bottom:1px solid #D9D9D9;
				span{
					display: inline-block;
					width:40%;
					text-align:center;
					vertical-align: bottom;
					@include sc(0.7rem,#929292);
				}
				span:last-child{
					width:20%;
					color:#F46A6A;
					text-decoration: underline;
				}				
			}
			li:not(:first-child){
				ul.details-list{
					&::after{
						display: table;
						content: "";
					}
					background:#F5F5F5;
					padding-left:0.15rem;
					li span{
						@include sc(0.7rem,#929292);	
					}
					li:last-child{
						margin-bottom:0.2rem;
					}
				}
			}
		}
	}
	/* 模态框样式 */
	.dialog-modal{
		width:100%;
		height:100%;
		position:fixed;
		/* top:2.2rem; */
		top:0;
		left:0;
		background:rgba(102,102,102,0.8);
		z-index:99;
		.dialog-wrapper{
			width:13rem;
			background:#fff;
			@include center;
			@include borderRadius(0.2rem);
			.dialog-container{
				&::before{
					display: table;
					content: "";
				}
				&::after{
					display: table;
					content: "";
				}
				@include sc(0.7rem,#000);
				text-align:center;
				border-bottom:1px solid #D9D9D9;
				li{
					width:67%;
					margin:0 auto;
					margin-bottom:1rem;
				}
				li.container-title{
					@include sc(0.85rem,#000);
					margin-top:0.5rem;
				}
				li.tips{
					color:#929292;
				}
				
			}
			.dialog-close-btn{
				p{
					width:50%;
					height:2rem;
					line-height:2rem;
					text-align: center;
					span{
						@include sc(0.7rem,#333);
					}
				}
				p:first-child{
					border-right:1px solid #D9D9D9;
				}
				p.removeD span{
					color:#F67676;
				}
			}
		}
	}
</style>


